.categories_menu .categories_mega_menu {
  display: none;
}

.categories_menu.active .categories_mega_menu {
  display: block;
}

.categories_menu {
  position: relative;
}

.categories_title {
  cursor: pointer;
  background-color: #060e2a;
  padding: -2px;
  color: #fff;
  font-size: 20px;
}
.categories_mega_menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #fff;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 15px;
  width: 100%;
  transition: height 0.3s ease;
  overflow: hidden;
}

.categories_menu.active .categories_mega_menu {
  display: block;
}

.categories_menu_toggle {
  position: relative;
  height: 0px;
}

.categories_menu_toggle ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  text-align: left;
}

.categories_mega_menu {
  display: flex;
}

.categories_mega_menu li {
  flex: 1;
  margin-right: 10px;
}

.header_wishlist {
  position: relative;
  display: inline-block;
}

.header_wishlist .dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  min-width: 50px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border-radius: 5px;
  right: 0;
  top: 50px;
}

.header_wishlist:hover .dropdown-content {
  display: block;
}

.dropdown-content div:hover {
  background-color: #ddd;
}

.search-results-dropdown {
  position: absolute;
  background-color: white;
  border: 1px solid #ddd;
  width: 100%;
  max-height: 300px;
  overflow-y: auto;
  z-index: 1000;
  margin-top: 5px;
}

.search-results-dropdown li {
  padding: 8px 16px;
  border-bottom: 1px solid #ddd;
}

.search-results-dropdown li:last-child {
  border-bottom: none;
}

.search-results-dropdown li a {
  color: black;
  text-decoration: none;
}

.search-results-dropdown li:hover {
  background-color: #f5f5f5;
}
